import React, { useEffect, useState, useMemo, ReactElement } from 'react';
import { render } from 'react-dom';
import { StaticContext } from 'react-router';
import { Route, Redirect, Link, Switch, RouteComponentProps } from 'react-router-dom';
import './App.css';
import { TournamentStatus, Player, TournamentSettings } from './interfaces';
import { GET } from './utils/requests';
import RoundSwitcher from './tournament/RoundSwitcher';
import CrossTable from './tournament/CrossTable';
import Chart from './tournament/Chart';
import NotFound from './404';
import styles from './css/nav.module.css';
import { FaLink, FaWindowClose } from 'react-icons/fa';
import { IoMdDocument } from 'react-icons/io';
import Pairings from './tournament/Pairings';
import * as regexes from './utils/regexes';
import TV from './tournament/Streams';
import Draggable from './components/DraggableSticky';

export default function Tournament(props: RouteComponentProps<any, StaticContext, any>) {

    const id = props.match.params.id
	const [loaded, setLoaded] = useState(undefined as boolean | undefined);
    const [settings, setSettings] = useState({} as TournamentSettings);
    const [status, setStatus] = useState({} as TournamentStatus);
	const [players, setPlayers] = useState([] as Player[]);
	const animate = useMemo(() => props.history.location.state && props.history.location.state.from === '/', [props.history.action]);
	const [poppedComponent, pop] = useState(null as null | HTMLDivElement);

    const playerDict: Map<string, Player> = useMemo(() => {
        if (!players.length) return new Map();
        let dict = new Map();
        for (let p of players) dict.set(p.id, p);
        return dict;
    }, [players]);

	const round = status.round || 0;
	
	useEffect(() => {
		GET({ url: '/tournament/' + id + '/getSettings'})
			.then(setSettings)
	}, [setSettings, id]);

    useEffect(() => {
		GET({ url: '/tournament/' + id + '/getStatus'})
			.then(setStatus)
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
    }, [setStatus, setLoaded]);

	useEffect(() => {
		GET({ url: '/tournament/' + id + '/fetchPlayers'})
			.then(setPlayers)
	}, [setPlayers, id]);

    useEffect(() => {
        if (!round) return;
        let histories = new Array(round + 1);
        for (let i = 1; i < round + 1; i++) histories[i] = {
            match: 0,
            game: 0
        };
        playerDict.set('bye', {
            id: 'bye',
            firstName: 'BYE',
            histories
        } as any as Player);
	}, [playerDict, round]);

	if (typeof loaded === 'undefined') return null;
	if (!loaded) return <NotFound />;

    return (

        <div id='main' className={animate ? 'animated-right' : ''}>
            <div className='container'>
				<div className={styles.container}>
					<div className={styles.profilePicture}>
						{settings.logoURL ?
							<img src={settings.logoURL} /> : 
							<div className={styles.defaultProfilePicture}>
								{status.name.match(regexes.lowerCase)?.map(v => v.substring(0, 1).toUpperCase()).join('')}
							</div>
						}</div>
					<div><h1>{status.name}</h1></div>
					<div>
						{settings.profileURL ? <a className={['header-button', styles.website].join(' ')} 
							href={settings.profileURL}
							target='_blank'
							rel='noopener noreferrer'
						>
							<FaLink /> Website
						</a> : null}
						{settings.rulesURL ? <a className={['header-button', styles.website].join(' ')} 
							href={settings.rulesURL}
							target='_blank'
							rel='noopener noreferrer'
						>
							<IoMdDocument /> Rules
						</a> : null}
					</div>
				</div>
				<section>
					<Route path='/:id/:section/:round?/' render={(rProps) => {
						let round = parseInt(rProps.match.params.round);
						if (isNaN(round)) round = status.round;
						if (round < 0 || round > settings.totalRounds) return null;
						return <RoundSwitcher
							section={rProps.match.params.section}
							round={round}
							id={props.match.params.id}
							playerDict={playerDict}
							status={status}
							settings={settings}
						/>
					}} />
					<Route render={({location}) => {
						return (
							<Switch location={location}>
								<Route exact path='/:id/' render={() => {
									return <Redirect push to={{
										pathname: `/${settings.vanityURL || props.match.params.id}/pairings`,
										state: props.history.location.state
									}}/>
								}}/>
								<Route path='/:id/stats/' render={() => {
									return <Chart
										round={status.round}
										status={status}
										players={Array.from(playerDict.values())}
										settings={settings}
									/>
								}} />
								<Route path='/:id/tv/' render={() => {
									return <TV
										streamURL={settings.streamURL}
										pop={pop}
									/>
								}} />
								<Route exact path='/:id/:section/' render={(rProps) => {
									return <Redirect push to={{
										pathname: rProps.match.url + (rProps.match.url.endsWith('/') ? '' : '/') + status.round,
										state: props.history.location.state
									}}/>
								}} />
								<Route path='/:id/pairings/:round' render={(rProps) => {
									let round = parseInt(rProps.match.params.round);
									if (isNaN(round)) return null;
									if (round < 0 || round > settings.totalRounds) return null;
									return <Pairings
										round={round}
										id={props.match.params.id}
										playerDict={playerDict}
										status={status}
										settings={settings}
									/>
								}}/>
								<Route path='/:id/standings/:round' render={(rProps) => {
									let round = parseInt(rProps.match.params.round);
									if (isNaN(round)) return null;
									if (round < 0 || round > settings.totalRounds) return null;
									return <CrossTable
										round={round}
										setDisplayPlayer={() => {}}
										show={() => {}}
										status={status}
										players={Array.from(playerDict.values())}
										settings={settings}
									/>
								}} />
								<Route path='/:id/*' component={NotFound} status={404} />
							</Switch>
						);
					}}/>				
					</section>
					<Draggable margin={10}>
						{poppedComponent ? <div onClick={() => pop(null)}><FaWindowClose /></div> : null}
						<div dangerouslySetInnerHTML={{
							__html: poppedComponent?.outerHTML || ''
						}}/>
					</Draggable>
            </div>
        </div>
    );
}